<template>
  <!-- 编辑简历 -->
  <div class="bg-wrap">
    <div class="main-box">
        <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
        label-position="left"
      >
        <div class="release-tit">
          <label>1</label>
          <h4>公司基本信息</h4>
        </div>
        <el-form-item label="头像">
            <div><img src="" alt=""></div>
          </el-form-item>
        <el-form-item label="姓名" props='recruit_type'>
            <el-input v-model="ruleForm.recruit_type"></el-input>
          </el-form-item>
        <el-form-item label="性别" props='recruit_type'>
            男
          </el-form-item>
        <el-form-item label="出生日期" props='recruit_type'>
            <el-date-picker
      v-model="ruleForm.recruit_type"
      type="date"
      placeholder="请选择公司成立时间">
    </el-date-picker>
          </el-form-item>
        <el-form-item label="当前求职状态" props='recruit_type'>
            <el-select
              v-model="ruleForm.recruit_type"
              placeholder="请选择活动区域"
            >
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <div class="release-tit">
          <label>2</label>
          <h4>学历信息</h4>
        </div>
        <el-form-item label="学校名称" props='recruit_type'>
            <el-input v-model="ruleForm.recruit_type"></el-input>
          </el-form-item>
          <el-form-item label="学历" props='recruit_type'>
            <el-select
              v-model="ruleForm.recruit_type"
              placeholder="请选择学历"
            >
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="所学专业" props='recruit_type'>
            <el-input v-model="ruleForm.recruit_type"></el-input>
          </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  name: "editResume",
  data(){
      return{
          rules: {},
      ruleForm: {
        recruit_type: "",
        position_name:"",
        work_city:'',
        work_adr:'',
        exp_req:'',
        edu_req:''
      },
      }
  }
};
</script>


